<template>
  <div class="client-view">
    <el-form ref="formRef" label-position="top" :rules="formRules" :model="ruleForm">
      <el-form-item
        v-for="comp in formJsonList"
        :key="comp.uuid"
        :prop="comp.uuid"
        :label="comp.title"
      >
        <component
          :is="comp.viewComponent"
          :compData="comp"
          v-model="ruleForm[comp.uuid]"
        ></component>
      </el-form-item>
    </el-form>
    <slot name="btns">
      <el-button type="primary" size="small" @click="validateForm"></el-button>
      <el-button type="danger" size="small" @click="resetForm"></el-button>
    </slot>
  </div>
</template>

<script>
import componentMap from '../componentMap';

export default {
  name: 'EasyFormBuild',
  data() {
    return {
      formJsonList: [],
      ruleForm: {},
      formRules: {},
    };
  },
  methods: {
    /* - */
    initDefaultForm(data) {
      this.formJsonList = data.map((comp) => ({
        ...comp,
        viewComponent: componentMap[comp.type]().viewComponent,
      }));
      this.initFormRender();
    },
    /*  */
    initFormRender() {
      this.formJsonList.forEach((comp) => {
        // 
        if (comp.defaultValue !== undefined && comp.defaultValue !== '') {
          // eslint-disable-next-line operator-linebreak
          this.ruleForm[comp.uuid] =
            typeof comp.defaultValue === 'object'
              ? JSON.parse(JSON.stringify(comp.defaultValue))
              : comp.defaultValue;
        }

        // 
        if (comp.type === 'BaseInput') {
          this.formRules[comp.uuid] = [
            { required: comp.required, message: '', trigger: 'blur' },
          ];
        } else if (
          comp.type === 'BaseRadio' ||
          comp.type === 'BaseCheckbox' ||
          comp.type === 'BaseSelect'
        ) {
          this.formRules[comp.uuid] = [
            { required: comp.required, message: '', trigger: 'change' },
          ];
        }
      });
    },
    /* - */
    setFormData(data) {
      if (typeof data !== 'object') return;

      Object.keys(this.ruleForm).forEach((key) => {
        if (data[key]) {
          this.ruleForm[key] = data[key];
        }
      });
    },
    /*  */
    validateForm() {
      return new Promise((resolve, reject) => {
        const { formRef } = this.$refs;
        if (!formRef) return;

        formRef.validate((valid, fields) => {
          if (valid) {
            console.log(
              'submit!===ruleForm: ',
              this.ruleForm,
              '======formJsonList: ',
              this.formJsonList
            );
            this.$emit('validateSuccess', this.ruleForm, this.formJsonList);
            resolve(this.ruleForm, this.formJsonList);
          } else {
            console.log('error submit!', fields);
            reject();
          }
        });
      });
    },
    /*  */
    resetForm() {
      const { formRef } = this.$refs;
      if (!formRef) return;
      formRef.resetFields();
    },
  },
};
</script>

<style lang="less" scoped></style>
